import ServiceCard from "../elements/service_card/service_card";
import desktopServiceIcon from "../../../public/serviceIcons/desktopIcon.png"
import backendIcon from "../../../public/serviceIcons/backendIcon.png"
import mobileIcon from "../../../public/serviceIcons/mobileIcon.png"
import designIcon from "../../../public/serviceIcons/designIcon.png"

export default function Services() {
    return (
      <div className="flex flex-col items-center justify-center">
        <ServiceCard icon={desktopServiceIcon} title="Icon for web development service card" description="We design and develop websites that are visually appealing and user-friendly."/>\
        <ServiceCard icon={backendIcon} title="Icon for backend development service card" description="We design and develop websites that are visually appealing and user-friendly."/>
        <ServiceCard icon={mobileIcon} title="Icon for mobile development service card" description="We design and develop websites that are visually appealing and user-friendly."/>
        <ServiceCard icon={designIcon} title="Icon for design service card" description="We design and develop websites that are visually appealing and user-friendly."/>
      </div>
    );
}
  